<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input :clearable="true" placeholder="/" v-model="SearchData" @clear="GetStudentList">
            <el-button slot="append" icon="el-icon-search" @click="GetStudent"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="AddStudentDialog" :disabled="UserInfo.identity===''||UserInfo.identity===''?true:false"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="StudentList.users" :border="true" :stripe="true">
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column prop="userId" label="/" min-width="90px"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="gender" label=""></el-table-column>
        <el-table-column prop="identity" label=""></el-table-column>
        <el-table-column prop="phone" label="" min-width="90px"></el-table-column>
        <el-table-column label="" width="185px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="EditStudentInfoDialog(scope.row)" :disabled="UserInfo.identity===''||UserInfo.identity===''?true:false"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="DeleteStudent(scope.row.userId)" :disabled="UserInfo.identity===''||UserInfo.identity===''?true:false"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="StudentList.pagenum" :page-sizes="[1,5,8,10,50,100,200,500]" :page-size="QueryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="StudentList.total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="AddStudentDialogVisible" width="40%" @close="CloseAddStudentDialog">
      <!-- tabs -->
      <el-tabs type="border-card" :stretch="true">
        <el-tab-pane>
          <span slot="label"></span>
          <!--  -->
          <el-form :model="AddStudentForm" :rules="AddStudentFormRules" ref="AddStudentFormRef" label-width="100px" label-position="left">
            <el-form-item label="/" prop="userId">
              <el-input v-model="AddStudentForm.userId"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input v-model="AddStudentForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="AddStudentForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="gender">
              <el-radio-group v-model="AddStudentForm.gender">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="phone">
              <el-input v-model="AddStudentForm.phone"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="AddStudentDialogVisible = false"> </el-button>
        <el-button type="primary" @click="AddStudent"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="EditDialogVisible" width="40%" @close="CloseEditStudent">
      <!--  -->
      <el-form :model="EditStudentInfo" :rules="AddStudentFormRules" ref="EditStudentFormRef" label-width="100px" label-position="left">
        <el-form-item label="/">
          <!--  -->
          <el-input v-model="EditStudentInfo.id" disabled></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="password">
          <el-input v-model="EditStudentInfo.password"></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="name">
          <el-input v-model="EditStudentInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-radio-group v-model="EditStudentInfo.gender">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="EditStudentInfo.phone"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditDialogVisible = false"> </el-button>
        <el-button type="primary" @click="EditStudent"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LoginVue from '../Login.vue'
export default {
  data () {
    return {
      UserInfo: {},
      // 
      QueryInfo: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10,
      },
      // 
      EditDialogVisible: false,
      // 
      EditStudentInfo: {},
      // 
      StudentList: {
        // 
        total: 5,
        // 
        pagenum: 1,
        // 
        users: [{
          userId: '12345678901',
          name: 'xiaobing',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG181',
          phone: '13118573647',
        }, {
          userId: '12345678902',
          name: 'xiaoai',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG182',
          phone: '13118545646',
        }, {
          userId: '12345678903',
          name: 'xiaoyi',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG181',
          phone: '13118573647',
        }, {
          userId: '12345678904',
          name: 'xiaobing',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG181',
          phone: '13118573647',
        }, {
          userId: '12345678905',
          name: 'xiaoai',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG182',
          phone: '13118545646',
        },]
      },
      // 
      SearchData: '',
      // 
      AddStudentDialogVisible: false,
      // 
      AddStudentForm: {//
        userId: '',
        //
        password: '',
        //
        name: '',
        //
        gender: '',
        //
        identity: '',
        //
        yuanxi: '',
        //
        discipline: '',
        //
        whichClas: '',
        //
        phone: '',
        //
        score: 100
      },
      // 
      AddStudentFormRules: {
        userId: [{ required: true, message: "/", trigger: "blur" }],
        password: [{ required: true, message: "", trigger: "blur" }],
        identity: [{ required: true, message: "", trigger: "blur" }],
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: "2~10", trigger: "blur" },
        ],
        gender: [{ required: true, message: "", trigger: "blur" }],
        yuanxi: [{ required: true, message: "", trigger: "blur" }],
        discipline: [{ required: true, message: "", trigger: "blur" }],
        whichClas: [{ required: true, message: "", trigger: "blur" }],
        phone: [{ required: true, message: "", trigger: "blur" },
        { min: 11, max: 11, message: '11', trigger: 'blur' },
        {
          pattern: /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
          message: '', trigger: 'blur'
        },]
      },
    }

  },
  created () {
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
    this.GetStudentList()
  },
  methods: {
    // 
    async GetStudentList () {
      // 
      const { data: res } = await this.$http.post('/users/getAdmin', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.StudentList = res.data
      console.log('');
    },

    //   
    async GetStudent () {
      const { data: res } = await this.$http.post(`/users/getUser/`, { userId: this.SearchData, identity: '' })
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.StudentList = res.data
      console.log('');
    },
    // 
    handleSizeChange (NewSize) {
      // 
      this.QueryInfo.pagesize = NewSize
      this.QueryInfo.pagenum = 1
      // 
      this.GetStudentList()
      console.log('' + NewSize + '');
    },
    // 
    handleCurrentChange (NewPage) {
      this.QueryInfo.pagenum = NewPage
      // 
      this.GetStudentList()
      console.log('' + NewPage + '');
    },

    // 
    AddStudentDialog () {
      this.AddStudentDialogVisible = true
    },
    // 
    AddStudent () {
      this.$refs.AddStudentFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post('/user/register', this.AddStudentForm)
        // 
        console.log(res);
        if (res.status !== 200) return this.$message.error(res.message)
        // 
        this.GetStudentList()
        // 
        this.AddStudentDialogVisible = false
        this.$message.success('')
      })
    },
    // 
    CloseAddStudentDialog () {
      this.$refs.AddStudentFormRef.resetFields()
    },
    // 
    DeleteStudent (StudentId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // url
        const { data: res } = await this.$http.delete(`users/deleteUser/${StudentId}`)
        if (res.status !== 200) return this.$message.error(res.message)
        // 
        this.GetStudentList()
        // 
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    EditStudentInfoDialog (StudentInfo) {
      this.EditDialogVisible = true
      this.EditStudentInfo = StudentInfo
    },
    // 
    async EditStudent () {
      // url
      this.$refs.EditStudentFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post(`users/editUser/`, this.EditStudentInfo)
        if (res.status !== 200) return this.$message.error(res.message)
        // 
        this.GetStudentList()
        console.log(res);
        this.$message.success('')
        this.EditDialogVisible = false
      })
    },
    // 
    CloseEditStudent () {
      this.$refs.EditStudentFormRef.resetFields()
      this.EditStudentInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
.el-pagination {
  padding: 15px 0;
}
</style>