<script>
export default {
  data () {
    return {
      // 
      loginForm: {
        userId: '',
        password: '',
      },
      UserInfo: {
        userId: '12345678901',
        name: 'xiaobing',
        gender: '',
        identity: '',
        yuanxi: '',
        discipline: '',
        whichClas: 'BG181',
        phone: '13118573647',
      },
      //
      RegUserDialogVisible: false,
      //   
      RegUserForm: {
        //
        userId: '',
        //
        password: '',
        //
        name: '',
        //
        gender: '',
        //
        identity: '',
        //
        yuanxi: '',
        //
        discipline: '',
        //
        whichClas: '',
        //
        phone: '',
        //
        score: 100
      },
      // 
      loginFormRules: {
        userId: [
          { required: true, message: "", trigger: "blur" },
          { min: 9, max: 10, message: "9~10", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 20, message: "6~20", trigger: "blur" },
        ],
      },
      //
      RegUserFormRules: {
        userId: [{ required: true, message: "/", trigger: "blur" }, { min: 9, max: 10, message: "9~10", trigger: "blur" },],
        password: [{ required: true, message: "", trigger: "blur" }, { min: 6, max: 20, message: "6~20", trigger: "blur" }],
        identity: [{ required: true, message: "", trigger: "blur" }],
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: "2~10", trigger: "blur" },
        ],
        gender: [{ required: true, message: "", trigger: "blur" }],
        yuanxi: [{ required: true, message: "", trigger: "blur" }],
        discipline: [{ required: true, message: "", trigger: "blur" }],
        whichClas: [{ required: true, message: "", trigger: "blur" }],
        phone: [{ required: true, message: "", trigger: "blur" },
        { min: 11, max: 11, message: '11', trigger: 'blur' },
        {
          // /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
          pattern: /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
          message: '', trigger: 'blur'
        },]
      }
    };
  },
  methods: {
    // 
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields();
    },

    login () {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error('') //
        // 
        const { data: res } = await this.$http.post('/user/login', this.loginForm);
        console.log(res);
        if (res.status !== 200) return this.$message.error("");
        console.log(this.loginForm);
        this.UserInfo = res.data


        window.sessionStorage.setItem("token", res.token);
        // 
        // delete this.UserInfo['password']
        // 
        window.sessionStorage.setItem("UserInfo", JSON.stringify(this.UserInfo));
        // /home
        this.$router.push("/home");
      });
    },

    // 
    RegUserDialog () {
      this.RegUserDialogVisible = true
    },
    // 
    RegStudent () {
      this.RegUserForm.identity = ''
      this.$refs.RegUserRef.resetFields()
    },
    // 
    RegTeacher () {
      this.RegUserForm.identity = ''
      this.$refs.RegUserRef.resetFields()
    },
    // 
    RegAdmin () {
      this.RegUserForm.identity = ''
      this.$refs.RegUserRef.resetFields()
    },
    //
    RegUser () {
      if (this.RegUserForm.identity === '') {

      }
      this.$refs.RegUserRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        // 
        const { data: res } = await this.$http.post('/user/register', this.RegUserForm)
        // 
        if (res.status !== 200) return this.$message.error(res.message)
        // 

        if (this.RegUserForm.identity === '' || '') this.$message.success('')
        else this.$message.success('')
        this.RegUserDialogVisible = false
        console.log(res);
      })
    },
    //
    CloseRegUserDialog () {
      this.$refs.RegUserRef.resetFields()
    }
  },
}
