        <el-table-column prop="whichClas" label="">
        </el-table-column>
        <el-table-column prop="date1" label="" min-width="105">
        </el-table-column>
        <el-table-column prop="date2" label="" min-width="105">
        </el-table-column>
        <el-table-column prop="verifyStatus" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="id" label="id">
        </el-table-column>
        <el-table-column v-if="false" prop="approvaId" label="id">
        </el-table-column>
        <el-table-column v-if="false" prop="applyTime" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="myContent" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="resultStatus" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="opinion" label="">
        </el-table-column>
        <el-table-column label="" min-width="80">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" :disabled="UserInfo.identity===''?true:false" @click="ApprovaDialog(scope.row,scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="ApprovalData.pagenum" :page-sizes="[1,5,8,10,50,100,200,500]" :page-size="QueryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="ApprovalData.total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="ApprovaDialogVisible" width="60%">
      <div>
        <el-card style="margin-bottom:10px">
          <div>
            <h1></h1>
            <span>&emsp;&emsp;{{ApprovaRowData.myContent}}</span>
            <div class="apply">
              <div>
                <span>{{ApprovaRowData.name}}</span>
              </div>
              <div>
                <span>{{ApprovaRowData.applyTime}}</span>
              </div>
            </div>
          </div>
        </el-card>
        <el-form :model="ApprovaRowData" ref="OpinionRef" size="small">
          <div style="margin-bottom:10px"></div>
          <el-form-item>
            <el-input type="textarea" v-model="ApprovaRowData.opinion"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="RejectApply" :disabled="UserInfo.identity===''?true:false"> </el-button>
        <el-button type="primary" @click="AgreeApply" :disabled="UserInfo.identity===''?true:false"> </el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
export default {
  data () {
    return {
      // 
      QueryInfo: {
        // 
        userId: 0,
        class: '',//
        // 
        pagenum: 1,
        // 
        pagesize: 10,
      },
      // 
      CascadeKey: [],
      //   
