      //   
      options: [{
        value: 'dianxin',
        label: '',
        children: [{
          value: 'zidonghua',
          label: '',
          children: [{
            value: 'yizhi',
            label: 'BG181'
          }, {
            value: 'fankui',
            label: 'BG182'
          }, {
            value: 'xiaolv',
            label: 'BG191'
          }, {
            value: 'kekong',
            label: 'BG192'
          }]
        }, {
          value: 'daohang',
          label: '',
          children: [{
            value: 'cexiangdaohang',
            label: '181'
          }, {
            value: 'dingbudaohang',
            label: '182'
          }]
        }, {
          value: 'daohang',
          label: '',
          children: [{
            value: 'cexiangdaohang',
            label: '181'
          }, {
            value: 'dingbudaohang',
            label: '182'
          }]
        }]
      }, {
        value: 'zujian',
        label: '',
        children: [{
          value: 'basic',
          label: '',
          children: [{
            value: 'layout',
            label: '18'
          }, {
            value: 'color',
            label: '19'
          }, {
            value: 'typography',
            label: '20'
          }, {
            value: 'icon',
            label: '21'
          }]
        }, {
          value: 'form',
          label: '',
          children: [{
            value: 'radio',
            label: '18'
          }, {
            value: 'checkbox',
            label: '19'
          }, {
            value: 'input',
            label: '20'
          }, {
            value: 'input-number',
            label: '21'
          }]
        }]
      }, {
        value: 'ziyuan',
        label: '',
        children: [{
          value: 'axure',
          label: '',
          children: [{
            value: 'sketch',
            label: '18'
          }, {
            value: 'sketch',
            label: '19'
          }, {
            value: 'sketch',
            label: '20'
          }]
        }, {
          value: 'sketch',
          label: '',
          children: [{
            value: 'sketch',
            label: '18',
          }, {
            value: 'sketch',
            label: '19',
          }, {
            value: 'sketch',
            label: '20',
          },]
        }]
      }],

      // 
      UserInfo: [],
      //
      ApprovalData: {
        total: 6,
        // 
        pagenum: 1,
        // 
        ApplyList: [{
          approvaId: '',
          userId: '12345678901',
          name: 'xiaobing',
          applyTime: new Date(),
          date1: new Date(),
          date2: new Date(),
          myContent: '20xx22343',
          verifyStatus: '',//01
          resultStatus: '',//01
          removeStatus: '',//01
          auditor: '',//
          auditorId: '',//id
          opinion: '',//
          whichClas: 'BG182'
        },
        ]
      },
      // 
      ApprovaDialogVisible: false,
      // 
      ApprovaRowData: {
        // index: '',
      }
    }
  },
  created () {
    //  
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
    console.log(this.UserInfo);
    this.getApprovalData()
  },

  methods: {
    async getApprovalData () {
      const { data: res } = await this.$http.post('/leave/getVerify', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.ApprovalData = res.data

      // 
      if (this.ApprovalData.ApplyList.length != 0) {
        this.ApprovalData.ApplyList.forEach((value, index, arry) => {
          var applyTime = new Date(this.ApprovalData.ApplyList[index].applyTime)
          var date1 = new Date(this.ApprovalData.ApplyList[index].date1)
          var date2 = new Date(this.ApprovalData.ApplyList[index].date2)

          // 
          this.ApprovalData.ApplyList[index].applyTime = this.TimeConvert(applyTime)
          this.ApprovalData.ApplyList[index].date1 = this.TimeConvert(date1)
          this.ApprovalData.ApplyList[index].date2 = this.TimeConvert(date2)
        });
      }
    },

    // 
    async getStudentByclas () {
      //  pop() 
      this.QueryInfo.class = this.CascadeKey[2]
      console.log(this.QueryInfo);
      const { data: res } = await this.$http.post('/users/getClass', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.LeaveData = res.data
    },

    //   
    async GetStudent () {
      this.QueryInfo.userId = this.SearchData
      const { data: res } = await this.$http.post(`/leave/getVerifyByuserId/`, this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.ApprovalData = res.data
      this.$message.success('')
      // 
      if (this.ApprovalData.ApplyList.length != 0) {
        this.ApprovalData.ApplyList.forEach((value, index, arry) => {
          var applyTime = new Date(this.ApprovalData.ApplyList[index].applyTime)
          var date1 = new Date(this.ApprovalData.ApplyList[index].date1)
          var date2 = new Date(this.ApprovalData.ApplyList[index].date2)

          // 
          this.ApprovalData.ApplyList[index].applyTime = this.TimeConvert(applyTime)
          this.ApprovalData.ApplyList[index].date1 = this.TimeConvert(date1)
          this.ApprovalData.ApplyList[index].date2 = this.TimeConvert(date2)
        });
      }

    },

    // 
    handleSizeChange (NewSize) {
      // 
      this.QueryInfo.pagesize = NewSize
      this.QueryInfo.pagenum = 1
      // 
      this.getApprovalData()
    },
    // 
    handleCurrentChange (NewPage) {
      this.QueryInfo.pagenum = NewPage
      // 
      this.getApprovalData()
    },
    // 
    TimeConvert (time) {
      var year = time.getFullYear();
      var month = time.getMonth() + 1
      var day = time.getDate()
      return year + '' + month + '' + day + ''
    },
    //   
    ApprovaDialog (RowData) {
      this.ApprovaDialogVisible = true
      this.ApprovaRowData = RowData
      console.log(this.ApprovaRowData);
    },
    // 
    RejectApply () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // 
        var requestData = {
          id: this.ApprovaRowData.id,
          verifyStatus: '',
          resultStatus: "",
          opinion: this.ApprovaRowData.opinion,
          auditor: this.UserInfo.name,
          auditorId: this.UserInfo.userId
        }
        console.log(requestData);
        const { data: res } = await this.$http.post('leave/submitVerify/', requestData)
        console.log(res);
        if (res.status !== 200) return this.$message.error('')
        this.getApprovalData()
        this.$message.success('')
        this.ApprovaDialogVisible = false
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    AgreeApply () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // 
        var requestData = {
          id: this.ApprovaRowData.id,
          verifyStatus: '',
          resultStatus: "",
          opinion: this.ApprovaRowData.opinion,
          auditor: this.UserInfo.name,
          auditorId: this.UserInfo.userId
        }
        const { data: res } = await this.$http.post('leave/submitVerify/', requestData)
        console.log(res);
        if (res.status !== 200) return this.$message.error('')
        this.getApprovalData()
        this.ApprovaDialogVisible = false
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
  }
}
</script>

