<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <div class="FormStyle">
        <el-form :model="EditPasswordForm" status-icon :rules="EditPasswordFormRules" ref="EditPasswordFormRef" label-width="100px" label-position="left">
          <el-form-item label="" prop="OriginalPassword">
            <el-input type="password" v-model="EditPasswordForm.OriginalPassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="EditPasswordForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input type="password" v-model="EditPasswordForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm"></el-button>
            <el-button @click="resetForm"></el-button>
          </el-form-item>
        </el-form>

      </div>

    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.EditPasswordForm.checkPass !== '') {
          this.$refs.EditPasswordFormRef.validateField('checkPass')
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.EditPasswordForm.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      // 
      UserInfo: {},
      // 
      EditPasswordForm: {
        userId: '',
        OriginalPassword: '',
        password: '',
        checkPass: '',
      },
      EditPasswordFormRules: {
        OriginalPassword: [{ required: true, message: "", trigger: "blur" },
        { min: 6, max: 20, message: "6~20", trigger: "blur" },],
        password: [
          { validator: validatePass, trigger: 'blur' },
          { min: 6, max: 20, message: "6~20", trigger: "blur" },
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' },
          { min: 6, max: 20, message: "6~20", trigger: "blur" },
        ],
      }
    };
  },
  created () {
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
  },
  methods: {
    submitForm () {
      this.$refs.EditPasswordFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        // 
        // 
        this.EditPasswordForm.userId = this.UserInfo.userId
        // console.log(this.EditPasswordForm);
        const { data: res } = await this.$http.post(`/users/editPassword`, this.EditPasswordForm)
        console.log(res);
        if (res.status !== 200) return this.$message.error(res.message)
        this.$message.success('')
        this.resetForm()//
        // sessionStorage
        window.sessionStorage.clear();
        this.$router.push("/login");
      });
    },
    resetForm () {
      this.$refs.EditPasswordFormRef.resetFields();
    }
  }
}
</script>

<style lang="less" scoped>
.FormStyle {
  width: 50%;
  min-width: 300px;
  max-width: 800px;
}
</style>