<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-form :model="ApplyLeaveForm" :rules="ApplyLeaveRuls" ref="ApplyLeaveRef" label-width="100px">
        <el-form-item label="" required style="width: 60%;">
          <el-col :span="11">
            <el-form-item>
              <el-date-picker type="date" placeholder="" v-model="ApplyLeaveForm.date1" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item>
              <el-date-picker type="date" placeholder="" v-model="ApplyLeaveForm.date2" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="myContent" style="width: 60%;">
          <el-input type="textarea" :autosize="{ minRows: 8,}" v-model="ApplyLeaveForm.myContent"></el-input>
        </el-form-item>
        <el-form-item style="width: 60%;" class="buttons">
          <el-button type="info" @click="ResetApplyLeaveForm"></el-button>
          <el-button type="primary" @click="SubmitApplyLeave" :disabled="UserInfo.identity===''||UserInfo.identity===''?true:false"></el-button>
        </el-form-item>
      </el-form>
    </el-card>

  </div>
</template>

<script> 
export default {
  data () {
    return {
      // 
      UserInfo: {},
      // 
      ApplyLeaveForm: {
        name: '',//
        userId: '',//id
        applyTime: new Date(),//
        date1: '',//
        date2: '',//
        myContent: '',//
        verifyStatus: '',//01
        resultStatus: '',//01
        removeStatus: '',//01
        auditor: '',//
        auditorId: '',//id
        opinion: '',//
        whichClas: 'BG182'

      },
      // 
      ApplyLeaveRuls: {
        date1: [{ required: true, message: '', trigger: 'change' }],
        date2: [{ type: 'date', required: true, message: '', trigger: 'change' }],
        conmyContenttent: [{ required: true, message: '', trigger: 'change' }]
      },
    }
  },
  created () {
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
  },
  methods: {
    //   
    SubmitApplyLeave () {
      this.$refs.ApplyLeaveRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        // 
        this.ApplyLeaveForm.name = this.UserInfo.name
        this.ApplyLeaveForm.userId = this.UserInfo.userId
        this.ApplyLeaveForm.whichClas = this.UserInfo.whichClas
        console.log(this.ApplyLeaveForm);

        const { data: res } = await this.$http.post('/leave/apply', this.ApplyLeaveForm)
        console.log(res);
        // 
        if (res.status !== 200) return this.$message.error(res.message)
        this.$message.success(res.message)
        console.log(this.ApplyLeaveForm);
        this.ResetApplyLeaveForm()
      })
    },
    // 
    TimeConvert (time) {
      var year = time.getFullYear();
      var month = time.getMonth() + 1
      var day = time.getDate()
      return year + '' + month + '' + day + ''
    },
    //   
    ResetApplyLeaveForm () {
      this.$refs.ApplyLeaveRef.resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.buttons {
  display: flex;
  justify-content: flex-end;
}
</style>