<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="11 ">
        <!-- <el-col :span="6"> -->
        <!--  clearable  show-all-levels="false"-->
        <!-- <div class="block"> -->
        <!-- <span class="demonstration"> click </span> -->
        <!-- <el-cascader ref="cascaderHandle1" clearable v-model="CascadeKey" :show-all-levels="false" :options="options" :props="CascaderProps" @change="getStudentByclas" @clear="getLeaveData"></el-cascader>
          </div>
        </el-col> -->
        <el-col :span="7">
          <el-input :clearable="true" placeholder="" v-model="SearchData" @clear="getLeaveData">
            <el-button slot="append" icon="el-icon-search" @click="GetStudent"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="LeaveData.ApplyList" border style="width: 100%">
        <el-table-column prop="name" label="">
        </el-table-column>
        <el-table-column prop="userId" label="" min-width="120">
        </el-table-column>
        <el-table-column prop="whichClas" label="">
        </el-table-column>
        <el-table-column prop="date1" label="" min-width="105">
        </el-table-column>
        <el-table-column prop="date2" label="" min-width="105">
        </el-table-column>
        <el-table-column prop="resultStatus" label="">
        </el-table-column>
        <el-table-column prop="removeStatus" label="">
          <template slot-scope="scope">
            <span>{{scope.row.resultStatus===''?scope.row.removeStatus:''}}</span>
          </template>
        </el-table-column>
        <el-table-column v-if="false" prop="verifyStatus" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="auditorId" label="id">
        </el-table-column>
        <el-table-column v-if="false" prop="auditor" label="id">
        </el-table-column>
        <el-table-column v-if="false" prop="applyTime" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="myContent" label="">
        </el-table-column>
        <el-table-column v-if="false" prop="opinion" label="">
        </el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="FindDialog(scope.row)"></el-button>
            <el-button type="success" size="mini" @click="removeApply(scope.row)" :disabled="UserInfo.identity===''||scope.row.removeStatus===''?true:false"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="QueryInfo.pagenum" :page-sizes="[1,5,8,10,50,100,200,500]" :page-size="QueryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="LeaveData.total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="FindDialogVisible" width="60%">
      <div>
        <h1></h1>
        <span>&emsp;&emsp;{{LeaveRowData.myContent}}</span>
        <div class="apply">
          <div>
            <span>{{LeaveRowData.name}}</span>
          </div>
          <div>
            <span>{{LeaveRowData.applyTime}}</span>
          </div>
        </div>
      </div>
      <!--  -->
      <el-divider></el-divider>
      <el-row>
        <span>{{LeaveRowData.date1}}-{{LeaveRowData.date1}}</span>
        <div>{{LeaveRowData.auditor}}</div>
        <div>id{{LeaveRowData.auditorId}}</div>
        <div>{{LeaveRowData.verifyStatus}}</div>
        <div>{{LeaveRowData.resultStatus}}</div>
        <div>{{LeaveRowData.opinion}}</div>

      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="FindDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
export default {
  data () {
    return {
      // 
      UserInfo: {},
      // 
      QueryInfo: {
        class: '',//
        userId: 0,
        id: 0,
        // 
        pagenum: 1,
        // 
        pagesize: 10,
      },
      // 
      CascadeKey: [],
      //   
      SearchData: '',
      //   
      options: [{
        value: 'dianxin',
        label: '',
        children: [{
          value: 'zidonghua',
          label: '',
          children: [{
            value: 'yizhi',
            label: 'BG181'
          }, {
            value: 'fankui',
            label: 'BG182'
          }, {
            value: 'xiaolv',
            label: 'BG191'
          }, {
            value: 'kekong',
            label: 'BG192'
          }]
        }, {
          value: 'daohang',
          label: '',
          children: [{
            value: 'cexiangdaohang',
            label: '181'
          }, {
            value: 'dingbudaohang',
            label: '182'
          }]
        }, {
          value: 'daohang',
          label: '',
          children: [{
            value: 'cexiangdaohang',
            label: '181'
          }, {
            value: 'dingbudaohang',
            label: '182'
          }]
        }]
      }, {
        value: 'zujian',
        label: '',
        children: [{
          value: 'basic',
          label: '',
          children: [{
            value: 'layout',
            label: '18'
          }, {
            value: 'color',
            label: '19'
          }, {
            value: 'typography',
            label: '20'
          }, {
            value: 'icon',
            label: '21'
          }]
        }, {
          value: 'form',
          label: '',
          children: [{
            value: 'radio',
            label: '18'
          }, {
            value: 'checkbox',
            label: '19'
          }, {
            value: 'input',
            label: '20'
          }, {
            value: 'input-number',
            label: '21'
          }]
        }]
      }, {
        value: 'ziyuan',
        label: '',
        children: [{
          value: 'axure',
          label: '',
          children: [{
            value: 'sketch',
            label: '18'
          }, {
            value: 'sketch',
            label: '19'
          }, {
            value: 'sketch',
            label: '20'
          }]
        }, {
          value: 'sketch',
          label: '',
          children: [{
            value: 'sketch',
            label: '18',
          }, {
            value: 'sketch',
            label: '19',
          }, {
            value: 'sketch',
            label: '20',
          },]
        }]
      }],

      // 
      FindDialogVisible: false,
      // 
      LeaveData: {
        // 
        total: 6,
        // 
        pagenum: 1,
        // 
        ApplyList: [{
          // id
          userId: '12345678901',
          // 
          name: 'xiaobing',
          // 
          applyTime: new Date(),
          // 
          date1: new Date(),
          // 
          date2: new Date(),
          // 
          myContent: '20xx22343',
          // 
          verifyStatus: '',
          resultStatus: '',//01
          removeStatus: '',//01
          auditor: '1',//
          auditorId: '',//id
          opinion: '',//
          whichClas: 'BG182'
        },]
      },
      LeaveRowData: {},
    }
  },
  created () {
    //  
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
    this.getLeaveData()

  },

  methods: {
    // 
    async getLeaveData () {
      console.log(this.UserInfo);
      // 
      if (this.UserInfo.identity === '') this.QueryInfo.userId = ''
      else if (this.UserInfo.identity == '') this.QueryInfo.userId = ''
      else this.QueryInfo.userId = this.UserInfo.userId
      const { data: res } = await this.$http.post('/leave/getResult', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      console.log(res);
      this.LeaveData = res.data

      // 
      if (this.LeaveData.ApplyList.length != 0) {
        this.LeaveData.ApplyList.forEach((value, index, arry) => {
          var applyTime = new Date(this.LeaveData.ApplyList[index].applyTime)
          var date1 = new Date(this.LeaveData.ApplyList[index].date1)
          var date2 = new Date(this.LeaveData.ApplyList[index].date2)

          // 
          this.LeaveData.ApplyList[index].applyTime = this.TimeConvert(applyTime)
          this.LeaveData.ApplyList[index].date1 = this.TimeConvert(date1)
          this.LeaveData.ApplyList[index].date2 = this.TimeConvert(date2)
        });
      }
    },

    // 
    async getStudentByclas () {
      //  pop() 
      this.QueryInfo.class = this.CascadeKey[2]
      console.log(this.QueryInfo);
      const { data: res } = await this.$http.post('/users/getClass', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.LeaveData = res.data
    },

    //   
    async GetStudent () {
      this.QueryInfo.userId = this.SearchData
      const { data: res } = await this.$http.post(`/leave/getResultByuserId/`, this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.LeaveData = res.data
      this.$message.success('')
      // 
      if (this.LeaveData.ApplyList.length != 0) {
        this.LeaveData.ApplyList.forEach((value, index, arry) => {
          var applyTime = new Date(this.LeaveData.ApplyList[index].applyTime)
          var date1 = new Date(this.LeaveData.ApplyList[index].date1)
          var date2 = new Date(this.LeaveData.ApplyList[index].date2)

          // 
          this.LeaveData.ApplyList[index].applyTime = this.TimeConvert(applyTime)
          this.LeaveData.ApplyList[index].date1 = this.TimeConvert(date1)
          this.LeaveData.ApplyList[index].date2 = this.TimeConvert(date2)
        });
      }

    },
    // 
    removeApply (rowData) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        // 
        var requestData = {
          id: rowData.id,
        }
        console.log(requestData);
        const { data: res } = await this.$http.post('leave/removeLeave/', requestData)
        console.log(res);
        if (res.status !== 200) return this.$message.error('')
        this.getLeaveData()
        this.$message.success('')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },

    // 
    handleSizeChange (NewSize) {
      // 
      this.QueryInfo.pagesize = NewSize
      this.QueryInfo.pagenum = 1
      // 
      this.getLeaveData()
    },
    // 
    handleCurrentChange (NewPage) {
      this.QueryInfo.pagenum = NewPage
      // 
      this.getLeaveData()
    },
    // 
    TimeConvert (time) {
      var year = time.getFullYear();
      var month = time.getMonth() + 1
      var day = time.getDate()
      return year + '' + month + '' + day + ''
    },
    // 
    FindDialog (RowData, index) {
      this.FindDialogVisible = true
      this.LeaveRowData = RowData
      this.LeaveRowData.index = index
      console.log(RowData);
    },

  }
}
</script>

<style lang="less" scoped>
.apply {
  text-align: right;
  margin-right: 10px;
}
h1 {
  text-align: center;
}
</style>