<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-descriptions title="" :column="2" border>
        <el-descriptions-item label="">{{UserInfo.userId}}</el-descriptions-item>
        <el-descriptions-item label="">{{UserInfo.name}}</el-descriptions-item>
        <el-descriptions-item label="">{{UserInfo.gender}}</el-descriptions-item>
        <el-descriptions-item label="">{{UserInfo.identity}}</el-descriptions-item>
        <el-descriptions-item label="">{{UserInfo.phone}}
        </el-descriptions-item>
        <el-descriptions-item v-if="UserInfo.yuanxi" label="">{{UserInfo.yuanxi}}
        </el-descriptions-item>
        <el-descriptions-item v-if="UserInfo.discipline" label="">{{UserInfo.discipline}}
        </el-descriptions-item>
        <el-descriptions-item v-if="UserInfo.whichClas" label="">{{UserInfo.whichClas}}
        </el-descriptions-item>
      </el-descriptions>
      <el-button type="primary" size="mini" style="margin-top:15px" @click="EditUserInfoDialog"></el-button>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="EditUserInfoDialogVisible" width="60%">
      <!--  -->
      <el-form :model="UserInfo" :rules="EditUserInfoRules" ref="EditUserInfoRef" label-width="100px" label-position="left">
        <el-form-item label="">
          <el-input v-model="UserInfo.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="UserInfo.identity" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="UserInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-radio-group v-model="UserInfo.gender">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="UserInfo.phone"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditUserInfoDialogVisible = false"> </el-button>
        <el-button type="primary" @click="EditUserInfo()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      UserInfo: {},
      //   
      EditUserInfoDialogVisible: false,
      //
      EditUserInfoRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: "2~10", trigger: "blur" },
        ],
        gender: [{ required: true, message: "", trigger: "blur" }],
        phone: [{ required: true, message: "", trigger: "blur" },
        { min: 11, max: 11, message: '11', trigger: 'blur' },
        {
          pattern: /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/,
          message: '', trigger: 'blur'
        },]
      }
    }
  },
  created () {
    this.GetUserInfo()
  },
  methods: {
    //   
    GetUserInfo () {
      this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))
    },
    // 
    async refreshData () {
      const quer = {
        userId: this.UserInfo.userId,
        identity: this.UserInfo.identity
      }
      const { data: res } = await this.$http.post(`/users/getUser/`, quer)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      window.sessionStorage.setItem("UserInfo", JSON.stringify(res.data.users[0]));
      // 
      this.GetUserInfo()
    },
    // 
    EditUserInfoDialog () {
      this.EditUserInfoDialogVisible = true
    },
    // 
    EditUserInfo () {
      this.$refs.EditUserInfoRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post(`users/editUser/`, this.UserInfo)
        if (res.status !== 200) return this.$message.error(res.message)
        this.$message.success('')
        // 
        this.refreshData()
        this.EditUserInfoDialogVisible = false
      })

    }
  }
}
</script>

<style lang="less" scoped>
</style>