<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/home'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="11 ">
        <el-col :span="6">
          <!--  clearable  show-all-levels="false"-->
          <div class="block">
            <!-- <span class="demonstration"> click </span> -->
            <el-cascader ref="cascaderHandle1" clearable v-model="CascadeKey" :show-all-levels="false" :options="options" :props="CascaderProps" @change="getStudentByclas" @clear="GetStudentList"></el-cascader>
          </div>
        </el-col>
        <el-col :span="7">
          <el-input :clearable="true" placeholder="" v-model="SearchData" @clear="GetStudentList">
            <el-button slot="append" icon="el-icon-search" @click="GetStudent"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="StudentList.users" :border="true" :stripe="true">
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column prop="userId" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="gender" label=""></el-table-column>
        <el-table-column prop="yuanxi" label=""></el-table-column>
        <el-table-column prop="discipline" label=""></el-table-column>
        <el-table-column prop="whichClas" label=""></el-table-column>
        <el-table-column prop="score" label=""></el-table-column>
        <el-table-column label="" min-width="180px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="EditScoreDialog(scope.row.userId)" :disabled="UserInfo.identity===''?true:false"></el-button>
            <el-button size="mini" type="success" icon="el-icon-arrow-down" @click="LookScoreDialog(scope.row.userId)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="StudentList.pagenum" :page-sizes="[1,5,8,10,50,100,200,500]" :page-size="QueryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="StudentList.total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="EditScoreDialogVisible" width="45%" @close="CloseEditScoreDialog">
      <!--  -->
      <el-form :model="EditScoreForm" ref="EditScoreFormRef" label-width="100px" label-position="left" :rules="EditScoreRules">
        <el-form-item label="" prop="type">
          <el-radio-group v-model="EditScoreForm.type">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input v-model="EditScoreForm.score"></el-input>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-input type="textarea" v-model="EditScoreForm.reason"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="EditScoreDialogVisible = false"> </el-button>
        <el-button type="primary" @click="EditScore" :disabled="UserInfo.identity===''?true:false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="LookScoreDialogVisible" width="60%">
      <el-table :data="StudentScore" border style="width: 100%">
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column prop="teacher" label="">
        </el-table-column>
        <el-table-column prop="time" label="">
        </el-table-column>
        <el-table-column prop="type" label="">
        </el-table-column>
        <el-table-column prop="score" label="">
        </el-table-column>
        <el-table-column prop="reason" label="">
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="LookScoreDialogVisible = false"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

