          phone: '13118573647',
          score: 90
        }, {
          userId: '12345678904',
          name: 'xiaobing',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG181',
          phone: '13118573647',
          score: 90
        }, {
          userId: '12345678905',
          name: 'xiaoai',
          gender: '',
          identity: '',
          yuanxi: '',
          discipline: '',
          whichClas: 'BG182',
          phone: '13118545646',
          score: 90
        },]
      },
      // 
      CascaderProps: {
        expandTrigger: 'hover',//
        value: 'label',//
        label: 'label',//
        children: 'children'//
      },
      // 
      CascadeKey: [],
      //   
      SearchData: '',
      //   
      options: [{
        value: 'dianxin',
        label: '',
        children: [{
          value: 'zidonghua',
          label: '',
          children: [{
            value: 'yizhi',
            label: 'BG181'
          }, {
