            label: '21'
          }]
        }]
      }, {
        value: 'ziyuan',
        label: '',
        children: [{
          value: 'axure',
          label: '',
          children: [{
            value: 'sketch',
            label: '18'
          }, {
            value: 'sketch',
            label: '19'
          }, {
            value: 'sketch',
            label: '20'
          }]
        }, {
          value: 'sketch',
          label: '',
          children: [{
            value: 'sketch',
            label: '18',
          }, {
            value: 'sketch',
            label: '19',
          }, {
            value: 'sketch',
            label: '20',
          },]
        }]
      }],
      // 
      EditScoreDialogVisible: false,
      // id
      StudentId: '',
      // 
      EditScoreForm: {
        userId: '',
        time: '',
        type: '',
        score: 0,
        reason: '',
        teacher: '',
      },
      // 
      EditScoreRules: {
        type: [{ required: true, message: "", trigger: "blur" }],
        score: [{ required: true, message: "", trigger: "blur" }],
        reason: [{ required: true, message: "", trigger: "blur" }],
      },
      // 
      LookScoreDialogVisible: false,

      // 
      StudentScore: [{
        userId: '12345678901',
        name: '',
        time: new Date(),
        type: '',
        score: 0,
        reason: ''
      },],
    }
  },
  created () {
    this.UserInfo = JSON.parse(window.sessionStorage.getItem('UserInfo'))

    this.GetStudentList()
    console.log(this.EditScoreForm.teacher);
  },
  methods: {
    // 
    async GetStudentList () {
      const { data: res } = await this.$http.post('/users/getStudent', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.StudentList = res.data
    },

    // 
    async getStudentByclas () {
      //  pop() 
      this.QueryInfo.class = this.CascadeKey[2]
      console.log(this.QueryInfo);
      const { data: res } = await this.$http.post('/users/getClass', this.QueryInfo)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.StudentList = res.data
    },

    //   
    async GetStudent () {
      const { data: res } = await this.$http.post(`/users/getUser/`, { userId: this.SearchData, identity: '' })
      console.log(res);
      if (res.status !== 200) return this.$message.error('')
      this.StudentList = res.data
    },

    // 
    handleSizeChange (NewSize) {
      // 
      this.QueryInfo.pagesize = NewSize
      // 
      this.GetStudentList()
      console.log('' + NewSize + '');
    },
    // 
    handleCurrentChange (NewPage) {
      this.QueryInfo.pagenum = NewPage
      // 
      this.GetStudentList()
      console.log('' + NewPage + '');
    },

    // 
    EditScoreDialog (NewId) {
      this.EditScoreDialogVisible = true
      // 
      this.EditScoreForm.teacher = JSON.parse(window.sessionStorage.getItem('UserInfo')).name
      this.EditScoreForm.time = new Date()
      this.EditScoreForm.userId = NewId
      console.log(this.EditScoreForm);
    },
    //
    EditScore () {
      // 
      this.$refs.EditScoreFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post(`/score/editScore`, this.EditScoreForm)
        if (res.status !== 200) return this.$message.error('')
        console.log(res);
        this.$message.success('')
        // 
        this.GetStudentList()
        this.EditScoreDialogVisible = false
      })
    },
    // 
    CloseEditScoreDialog () {
      this.$refs.EditScoreFormRef.resetFields()
    },
    // 
    async LookScoreDialog (NewId) {
      this.LookScoreDialogVisible = true
      // 
      const { data: res } = await this.$http.get(`/score/getScore/${NewId}`)
      console.log(res);
      if (res.status !== 200) return this.$message.error('')

      this.StudentScore = res.data.score//

      // 
      if (this.StudentScore.length != 0) {
        this.StudentScore.forEach((value, index, arry) => {
          var time = new Date(this.StudentScore[index].time)
          // 
          this.StudentScore[index].time = this.TimeConvert(time)
        });
      }
    },
    // 
    TimeConvert (time) {
      var year = time.getFullYear();
      var month = time.getMonth() + 1
      var day = time.getDate()
      return year + '' + month + '' + day + ''
    }
  }
}
</script>

<style lang="less" scoped>
.el-pagination {
  padding: 15px 0;
}
</style>